.skills-section {
    padding: 100px 0;
}

.skills {
    margin-top: 50px;
}

.skill-area {
    vertical-align: top;
    width: 300px;
    display: inline-block;
}

.skill-bubble {
    display: inline-block;
    background-color: #eee;
    margin: 2px 3px;
    padding: 5px;
    border-radius:999px;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}
.skill-bubble:hover {
    background-color: #d3d3d3;
}

.skill-icon {
    height: 200px;
}


